.gallery-block {
    margin-top: 20px;
    margin-bottom: 20px;

    padding-right: 20px;
    padding-left: 20px;

    @media screen and (min-width: $tablet) {
        margin-top: 60px;
        margin-bottom: 50px;

        padding-right: 71px;
        padding-left: 71px;
    }
}

.list {
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    align-items: center;

    @media screen and (min-width: $tablet) {
        display: flex;
        flex-flow: row wrap;
        margin-top: -30px;
    }

    @media screen and (min-width: $desktop) {
        justify-content: center;
        margin-top: -30px;
    }

}

.list__item {
   
    @media screen and (min-width: $tablet) {
        flex-basis: calc(100% / 2 - 40px);
        margin-top: 30px;

        &:nth-child(2n) {
            margin-left: 20px;
        }
    }

    @media screen and (min-width: $desktop) {
        flex-basis: calc(100% / 3 - 60px);
        margin-right: 30px;
        margin-top: 30px;


        &:nth-child(2n) {
            margin-left: 0;
        }

    }
}

.item__img {
    border-radius: 5px;
}

.item__title,
.item__genre {
    margin-top: 10px;

    font-weight: 500;
    font-size: 12px;
    line-height: 1.33;
    color: $secondary-text;
}

.item__genre {
    color: $accent;
}